import React from 'react';
import { Component } from 'react';
import '../Assets/animate.css';
class Menu extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
            <div id="leftMenu" onClick={this.OpenMenu}>
                <div className="LogoBtn">
                    <p>Home</p>
                </div>

                <ul id="menuList">
                    <a href='#home'><li className="menuListItem" id="homeLink">Home</li></a>
                    <a href='#about'><li className="menuListItem" id="aboutLink">About</li></a>
                    <a href='#venue'><li className="menuListItem" id="recentLink">Venue</li></a>
                    <a href='#artists'><li className="menuListItem" id="comingLink">Artists</li></a>
                </ul>
            </div>
            </React.Fragment>
         );
    }


}
 
export default Menu;